___CSS_LOADER_EXPORT___.push([module.id, `html {
    position: fixed;
    width: 100%;
    height: 100%;

    body {
        flex: 1;
        font-family: Noto Sans, sans-serif;
        display: flex;
        flex-direction: column;
        margin: 0;

        header {
            flex: 2;
            & nav {
                flex: 3;
                background-color: blue;
                display: flex;

                & button {
                    flex: 1;
                    margin-left: 5%;
                    margin-right: 5%;
                    border: 1px solid black;
                    background-color: pink;
                    border-style: double;
                }
            }
        }

        #content {
            flex: 6;

            div {
                flex: 1;
                border: 1px solid black;
                color: whitesmoke;
                text-align: center;

                background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});

                header {
                    font-size: 3em;
                    font-weight: bold;
                }

                p {
                    font-size: 1.5em;
                }
            }
        }

    }
}
